.feature-container {
    z-index: 2;
    width: 100%;
    height: auto;

    display: flex;
    flex-direction: row;
    align-items: center;

    position: relative;

    
}
.feature-container a{
    text-decoration: none;
    color: #FFFF;

}


.feature-container:hover > #FeaturePrevBtn {
    visibility: visible;
    opacity: 100%;
}
.feature-container:hover > #FeatureNextBtn {
    visibility: visible;
    opacity: 100%;
}



#FeatureNextBtn:hover, #FeaturePrevBtn:hover {
    filter: contrast(80%);
}

#FeatureNextBtn:active, #FeaturePrevBtn:active {
    filter: brightness(10%);
}



#FeaturePrevBtn {
    display: flex;
    position: absolute;
    z-index: 10;
    left: 0;
    cursor: pointer;
    transition: all 0.3s ease-in-out;


    top: 40%;
    user-select: none;

    visibility: hidden;
    opacity: 0%;
}
#FeatureNextBtn {
    display: flex;
    right: 0;
    position: absolute;
    z-index: 10;
    cursor: pointer;
    transition: all 0.3s ease-in-out;

    top: 40%;
    user-select: none;

    visibility: hidden;
    opacity: 0%;
}






.slide-content {
    z-index: 2;
    width: 100%;
    height: clamp(300px, 50vw, 580px);


    position: relative;
    margin: auto;

}



.slider {
    overflow-x: scroll;
    display: flex;
    flex-direction: row;
    position: relative;

    width: 100%;
    height: 100%;
    padding-inline: var(--inner-width);

    scroll-snap-type: x mandatory;

    overflow-x: auto;
    -webkit-overflow-scrolling: touch;

    -ms-overflow-style: none;
    scrollbar-width: none;

    gap: 10px;

    scroll-behavior: smooth;
}
.slider::-webkit-scrollbar {
    display: none;
}


.slide:hover  .feature-btn{
    display: flex;
}


.slide {
    border-radius: 20px;
    position: relative;

    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;

    transition: .6s;

    flex: none;


    scroll-snap-align: center;
    scroll-snap-stop: always;

    flex-shrink: 0;
}

.slide img {
    user-select: none;
    z-index: -2;
    position: absolute;
    
    width: 100%;
    height: 100%;

    object-fit: cover;
    
    border-radius: 20px;
}









.feature-intro {
    padding-inline: var(--inner-width);

    z-index: 2;
    height: 100%;
    width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;

    gap: 42px;


    color: #FFFFFF;

    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.45);
}





.feature-intro h3, p {
    z-index: 2;
}



.feature-info-title{
    display: flex;
    width: auto;
    height: auto;
    gap: 8px;
    flex-direction: column;

    max-width: 800px;
}

.feature-info-title h2{
    display: inline;
    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: clamp(21px, 5vw, 38px);

    text-transform: capitalize;

    margin-bottom: 1px;

    color: #ffff;
}

.feature-info-title h3 {
    font-family: 'Gilroy-Medium';
    font-style: normal;
    font-weight: 400;
    font-size: clamp(14px, 3vw, 20px);
}

.feature-info-title p {
    font-family: 'Gilroy-SemiBold';
    font-size: clamp(12px, 3vw, 14px);
    color: #cecece;

    color-interpolation-filters: linearRGB;
    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.45);

    display: -webkit-box;
    -webkit-line-clamp: 4;
    overflow: hidden;
    -webkit-box-orient: vertical; 
}




.feature-btn {
    display: flex;
    flex-direction: row;
    text-align: center;

    gap: 20px;
}

.align-btn {
    gap: 16px;
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    flex: none;
    order: 1;
    flex-grow: 0;

    text-align: center;

    justify-content: center;

    color: #FFF;
}

.align-btn p {
    user-select: none;

    width: 68px;
    height: 52px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.align-btn svg {
    width: 42px;
    height: 42px;
    user-select: none;
}




.playnow-btn, .watchlist-btn {
    cursor: pointer;

    width: 191px;
    height: 66px;
    border-radius: 3px;

    padding: 0px 32px;
    gap: 16px;

    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;


}


.playnow-btn {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(40px);
}

.playnow-btn:hover {
    background: rgba(24, 24, 24, 0.733);
}

.playnow-btn:active {
    background: rgba(255, 255, 255, 0.37);

    transition: all 0s;
}




.watchlist-btn {
    display: flex;
    flex-direction: row;
    align-items: center;

    justify-content: center;

    border: 1px solid #FFFFFF;

    background-color: rgba(128, 126, 129, 0.01);
}
.watchlist-btn svg{
    width: 20px;
    height: 20px;
}
.watchlist-btn svg rect{
    fill: #FFFFFF;
}


.watchlist-btn:hover {
    background: rgba(24, 24, 24, 0.733);
}


.watchlist-btn:active {
    background-color: #807e813d;

    transition: all 0s;
}







.subtitle{

    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 12px;
    flex-wrap: wrap;
}
.feature-info-title .voteAvarage h3{
    margin-bottom: 0px;
}


@media(max-width: 768px) {
    .feature-btn {
        display: none;
    }

    .feature-info-title p {
        -webkit-line-clamp: 3;
    }

    .feature-intro {
        width: 100%;
        height: 100%;
        max-width: 100%;
        gap: 0px;
        justify-content: flex-end;

        margin-bottom: 20px;
    }

    .feature-btn {
        padding: 0;
        flex-direction: row;
        padding-top: 13px;
        width: 100%;
        gap: 12px;

    }




    .playnow-btn, .watchlist-btn {
        cursor: pointer;

        min-width: 100px;
        width: auto;
        height: 50px;
        border-radius: 3px;

        padding: 0px 32px;
    }

    .playnow-btn svg{
        width: 28px;
        height: 28px;
    }



    #FeaturePrevBtn, #FeatureNextBtn {
        display: none;
    }

    .feature-container:hover .prev-btn {
        display: none;
    }
    .feature-container:hover  .next-btn {
        display: none;
    }
    
}





.filter {
    border-radius: 20px;
    user-select: none;
    z-index: -1;
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 0;
    margin: 0;
    top: 0;
    left: 0;
    background: linear-gradient(259.96deg, rgba(0, 0, 0, 0.60) 1.2%, rgba(0, 0, 0, 0.25) 47.01%, rgba(0, 0, 0, 0.60) 100%);
    mix-blend-mode: multiply;
}