@import './Variables.css';

@font-face {
    font-family: 'Gilroy-Bold';
    src: url(./layout/Fonts/EM-Gilroy/Gilroy-Bold.ttf) format('truetype');
}

@font-face {
    font-family: 'Gilroy-Regular';
    src: url(./layout/Fonts/EM-Gilroy/Gilroy-Regular.ttf) format('truetype');
}

@font-face {
    font-family: 'Gilroy-Medium';
    src: url(./layout/Fonts/EM-Gilroy/Gilroy-Medium.ttf) format('truetype');
}

@font-face {
    font-family: 'Gilroy-SemiBold';
    src: url(./layout/Fonts/EM-Gilroy/Gilroy-SemiBold.ttf) format('OpenType');
}


html{
    width: 100%;
    height: auto;
}

* {
    margin: 0;
    padding: 0;
    
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
}

body {
    width: 100%;
    height: auto;
    background: #0A070B;

}


footer{
    display: flex;
    flex-direction: column;
    align-items: center;

    background: #0A070B;
    margin-top: 40px;
    padding-block: 24px;
    padding-inline: 5%;

    border: solid #4F4E50;
    border-width: 5px 0px 0px 0px;
    gap: 24px;
}


#InfiniteCheck{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100px;
}





.navLogada{
    width: 100%;
    top: 0;
    left: 0;
    
}

.App{
    width: 100%;
    height: 100%;
    position: relative;
    background-image: linear-gradient(hsla(285, 22%, 4%, 0.024) 10% , hsla(285, 22%, 4%, 0.1) 20%,hsla(285, 22%, 4%, 0.8) 50%, #0a070b 90%);
}


#btnTop{
    width: 50px;
    height: 50px;
    position: fixed;
    right: 0;
    bottom: 60px;
    background-color: #F41B3B;
    border-radius: 4px 0px 0px 4px;

    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    cursor: pointer;

    z-index: 50;
}
#btnTop img{
    width: 30px;
    height: 30px;
}








header{
    width: 100%;
    height: auto;
    
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}
header > .Menu-Slider {
    z-index: 20;
    position: relative;
    padding-left: clamp(20px, 5vw, 70px);
    left: 0;
    top: 0;
    width: 100%;
    height: 60px;
    overflow: hidden;
    
}

@media screen and (min-width: 1280px) {
        .HeaderLogada .Menu-Slider {
        display: none;
        z-index: 20;
        position: relative;
        padding-left: clamp(20px, 5vw, 70px);
        left: 0;
        top: 0;
        width: 100%;
        height: 60px;
        overflow: hidden;
    }
}



