.LandingPage {
    font-family: 'Gilroy-SemiBold';
    width: 100vw;
    height: 100vh;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    color: #fff;
    text-decoration: none;

    gap: 10px;

    padding: 5%;
}

.LandingPage a {

    color: #878787;


    font-size: clamp(28px, 5vw, 78px);
    text-decoration: none;
}

.LandingPage div {
    display: flex;
    flex-direction: column;
    align-items: center;

    justify-content: center;
    font-family: 'Gilroy-SemiBold';
    color: #878787;


    font-size: clamp(28px, 5vw, 78px);
    text-decoration: none;

}


.LandingPage a:hover {

    color: #fff;
}

.LandingPage span {

    color: rgb(135, 3, 3);
    bottom: 0;
}