/* default style open */



.pesquisa-container {
    display: none;
}








.Profile-Content {
    width: auto;
    height: 46px;

    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 12px;

    cursor: pointer;

    position: relative;

    border-radius: 6px;

    padding: 0px 10px;
}

.Profile-Content:hover{
    background-color: #36353680;
    transition: all 0.3s ease-in-out;
}
.Profile-Content:active{
    background-color: #808080;
}

.mini-menu svg{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    
}


.Profile-Avatar {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    overflow: hidden;
}

.Profile-Avatar img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}


@keyframes wipe-in-down {
    from {
        clip-path: inset(0 0 100% 0);
    }

    to {
        clip-path: inset(0 0 0 0);
    }
}

[transition-style="in:wipe:down"] {
    animation: 2.5s cubic-bezier(.25, 1, .30, 1) wipe-in-down both;
}






#Card-Profile {
    top: 50px;
    right: 0;
    position: absolute;

    width: 257px;
    height: auto;

    background: #15161C;
    box-shadow: 0px 4px 45px rgba(2, 6, 13, 0.42);
    border-radius: 8px;

    display: none;
    flex-direction: column;
    align-items: center;


    animation: 0.5s cubic-bezier(.25, 1, .30, 1) wipe-in-down both;
}
#UserMenu-container{
    user-select: none;
}
#UserMenu-container.active #Card-Profile {
    display: flex;
}

.Card-avatar-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 14px;

    width: 100%;
    height: 150px;

    letter-spacing: -0.01em;
}
.Card-avatar img{
    width: 20px;
    height: 20px;
}
.Card-avatar-container h5 {
    font-family: 'Gilroy-Regular';
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: -0.01em;
    text-align: left;

    color: #FFFFFF;

    opacity: 0.65;
}

#mini-menu-ul{
    display: flex;
    flex-direction: column;

    width: 80%;
    height: auto;

    gap: 0;
    padding: 0;
    margin: 0;


}

.Card-Profile-Flex {
    cursor: pointer;
    user-select: none;

    height: 56px;
    width: 100%;

    border-top: 1px solid #ffffff0e;
    border-width: 1px;


    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    gap: 16px;


    letter-spacing: -0.01em;
}

.Card-Profile-Flex p {
    font-family: 'Gilroy-Medium';
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;


}

.Card-Profile-Flex h2 {
    font-family: 'Gilroy-Medium';
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #F84550;

}
.Card-Profile-Flex svg {
    width: 24px;
    height: 24px;
    transition: all 200ms ease-in-out;
}

.settingsIcon:hover svg{
    transition: all 400ms ease-in-out;
}
.settingsIcon:hover svg{
    transform: rotate(120deg);
}
  
.Card-Profile-Flex:hover svg{
    margin-left: 10px;
}   






.Card-avatar {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    overflow: hidden;
}

#Card-Profile img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}