/* default style open */

.Navigation {
    z-index: 50;
    width: 100%;
    height: auto;
    position: relative;
    height: var(--nav-height);


    display: flex;
    justify-content: space-between;
    align-items: center;

    gap: 24px;
    padding-top: 30px;
    padding-bottom: 30px;

    padding-inline: var(--inner-width);

}



/* Logo Navigation */

.NavLogo {
    order: 1;
    margin-right: 20px;
    width: clamp(100px, 20vw, 180px);
}




/* Lista de navegação */

.NavigationLinks {
    order: 2;

    display: flex;


    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 24px;

    margin-right: 50px;

    list-style-type: none;

}

.NavigationLinks li {
    user-select: none;

}

.NavigationLinks li svg path {
    fill: #737174;
}



.NavigationLinks li svg path,
.NavLinks {
    transition: all 0.3s ease-in-out;
}

.NavigationLinks li:hover svg path,
.NavigationLinks li:hover a {
    fill: #E8ECEF;
    color: #E8ECEF;
}




.NavLinks {
    display: flex;
    align-items: center;
    align-items: center;
    justify-content: center;
    list-style: none;

    gap: 8px;

    text-decoration: none;
    white-space: nowrap;

    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #807E81;

}

.NavLinks svg {
    display: flex;
    align-items: center;
    justify-content: center;
}

.NavLinks:checked {}


/* Selecionar linguagem */
#lang-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    position: relative;

    margin-right: 8px;

    user-select: none;
}



@keyframes wipe-in-down {
    from {
        clip-path: inset(0 0 100% 0);
    }

    to {
        clip-path: inset(0 0 0 0);
    }
}

[transition-style="in:wipe:down"] {
    animation: 2.5s cubic-bezier(.25, 1, .30, 1) wipe-in-down both;
}








#MultiLang {

    padding: 8px 12px;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    gap: 8px;

    cursor: pointer;

    border-radius: 6px;
}

#MultiLang svg {


    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    transition: all 300ms ease-in-out;
}

#MultiLang p {
    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #FFF;
}

#MultiLang:hover {
    background-color: #36353680;
    transition: all 0.3s ease-in-out;
}

#MultiLang:active {
    background-color: #808080;
}

#lang-container.active #select-options {
    display: flex;
}

#lang-container.active svg {
    transform: rotateX(180deg);
}



#select-options {
    display: none;
    position: absolute;
    flex-direction: column;

    min-width: 300px;
    gap: 0px;
    border-radius: 10px;
    right: 0;
    top: 40px;

    background-color: #15161C;

    animation: 0.5s cubic-bezier(.25, 1, .30, 1) wipe-in-down both;

    overflow: hidden;
}

#select-options span {

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;

    padding: 5px 20px;
}



#select-options span:hover {
    background-color: #57575759;
}

#select-options span:active {

    background-color: #575757c7;

}



#select-options input[type="radio"] {
    display: none;
}

#select-options label {
    position: relative;

    display: flex;
    align-items: center;

    width: 100%;
    height: 100%;

    color: #FFFF;

    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;


    padding: 10px 0px;

    cursor: pointer;

    border-radius: 5px;

    user-select: none;

}

#select-options label:before {
    content: "";
    height: 16px;
    width: 16px;

    border: 2px solid #ffffff;
    border-radius: 50%;
    margin-right: 10px;

    z-index: 50;
}

#select-options input[type="radio"]:checked+label {
    font-family: 'Gilroy-bold';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #FFF;
}

#select-options input[type="radio"]:checked+label::before {
    height: 10px;
    width: 10px;
    border: 5px solid #F9CC0D;
}








/* Botão Login */


.sing-btn {
    min-width: 59px;
    min-height: 25px;
    width: clamp(59px, 10vw, 98px);
    height: clamp(25px, 5vw, 42px);


    background: #F41B3B;
    border-radius: 3px;
    border: none;

    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    text-decoration: none;
}

.sing-btn p {
    user-select: none;

    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: clamp(12px, 2vw, 16px);
    line-height: 22px;
    color: #fff;
}

.sing-btn:active {
    background-color: #a7142a;
}

.signin-container {
    margin-left: auto;
    position: relative;
}

.signin-container span {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.signin-container span svg {
    display: flex;
    align-items: center;
    justify-content: center;


}

.pesquisa-container {
    display: none;
}

.nav-login {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    order: 3;
}

