/*      MobileFirst 
media query MobileFirst Open*/

@media screen and (max-width: 480px) {
    .Navigation {
        display: flex;
        flex-direction: row;

    }


    .Navigation ul {

        display: none;

        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        gap: 20px;
    }

    .Navigation ul li {
        list-style: none;
    }

    .Navigation ul li a {

        color: #807E81;
    }


}

/*
media query MobileFirst Close*/



/*      iPads/Tablets
media query MobileFirst Open*/

@media screen and (max-width: 750px) {
    #lang-container {
        display: none;
    }
}

/*      iPads/Tablets
media query MobileFirst Close*/

/*      Destok/ Large Screens
media query MobileFirst Open*/

@media screen and (min-width: 1025px) {



    .Navigation ul {
        padding: 20px;

        flex-direction: row;
        align-items: center;
        gap: 24px;
    }

    .Navigation ul li a {
        text-align: center;

        cursor: pointer;

        color: #807E81;

    }

    .Navigation ul li {
        cursor: pointer;
        height: 22px;


        display: flex;
        align-items: center;
        align-items: center;
        justify-content: center;
        list-style: none;

        gap: 8px;
    }

    .Navigation ul li svg {
        align-self: center;
        display: flex;
        align-items: center;
        justify-content: center;
    }



}



@media screen and (max-width: 946px) {
    .Navigation ul {
        padding: 20px;
        order: 4;
        flex-direction: row;
        align-items: center;
        gap: 24px;
    }
}

@media screen and (max-width: 946px) {
    .Navigation {
        flex-wrap: wrap;
        height: auto;

        padding-top: 30px;
        padding-bottom: 0px;

        gap: 0;
    }
}

@media screen and (max-width: 800px) {
    .Navigation ul {
        margin: 0;
        padding-inline: 0;
    }


}

@media screen and (max-width: 500px) {
    .Navigation ul {
        display: none;
        order: 2;
    }

    .Navigation {
        flex-wrap: nowrap;
        padding-bottom: 30px;
    }

}









@media screen and (max-width: 1279px) {


    .logo-container {
        order: 1;
        flex-basis: 20%;
        min-width: 106px;
        min-height: 28px;

        height: 0;
        display: flex;
        align-items: center;
    }
}


@media screen and (min-width: 1280px) {


    .Navigation ul {
        padding: 20px;

        display: flex;

        flex-direction: row;
        align-items: center;
        gap: 24px;
    }

    .logo-container {
        order: 1;

        max-width: 169px;
        max-height: 41px;

        height: 0;
        display: flex;
        align-items: center;
    }
}


