@keyframes wipe-in-right {
  from {
    clip-path: inset(0 100% 0 0);
  }

  to {
    clip-path: inset(0 0 0 0);
  }
}

[transition-style="in:wipe:right"] {
  animation: 2.5s cubic-bezier(.25, 1, .30, 1) wipe-in-right both;
}


.search-container{
  margin-right: 10px;
  display: flex;
  flex-direction: row;
  position: absolute;
  align-items: center;
  justify-content: center;
  left: -210px;
  min-width: 200px;
  
}

#btn-search{
  cursor: pointer;
  visibility: visible;
  opacity: 100%;
  position: absolute;

  right: 26px;

  transition: visibility 0s 0.3s, opacity 0.3s;
}

#btn-search.activeSearch {
  visibility: hidden;
  opacity: 0;
}

#pesquisa-container.activeSearch {
  -webkit-animation: scale-in-hor-right 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  animation: scale-in-hor-right 0.3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

#pesquisa-container.notActiveSearch {
  transform: scaleX(0);
  -webkit-animation: scale-out-hor-right 0.3s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
  animation: scale-out-hor-right 0.3s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
}









#pesquisa-container {
  overflow: hidden;
  min-width: 0px;
  height: 30px;
  display: flex;


  flex-direction: row;
  align-items: center;
  background-color: #202020;

  border-radius: 10px 10px;
}

@-webkit-keyframes scale-in-hor-right {
  0% {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: 100% 100%;
            transform-origin: 100% 100%;
    opacity: 1;
  }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: 100% 100%;
            transform-origin: 100% 100%;
    opacity: 1;
  }
}
@keyframes scale-in-hor-right {
  0% {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: 100% 100%;
            transform-origin: 100% 100%;
    opacity: 1;
  }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: 100% 100%;
            transform-origin: 100% 100%;
    opacity: 1;
  }
}

@-webkit-keyframes scale-out-hor-right {
  0% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: 100% 100%;
            transform-origin: 100% 100%;
    opacity: 1;
  }
  100% {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: 100% 100%;
            transform-origin: 100% 100%;
    opacity: 1;
  }
}
@keyframes scale-out-hor-right {
  0% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: 100% 100%;
            transform-origin: 100% 100%;
    opacity: 1;
  }
  100% {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: 100% 100%;
            transform-origin: 100% 100%;
    opacity: 1;
  }
}

#btn-close{
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


#pesquisa-container.activeSearch #btn-close{
  -webkit-animation: slide-right 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	animation: slide-right 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}


@-webkit-keyframes slide-right {
  0% {
    -webkit-transform: translateX(-300px);
            transform: translateX(-300px);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
@keyframes slide-right {
  0% {
    -webkit-transform: translateX(-300px);
            transform: translateX(-300px);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}








#pesquisa-container span {
  cursor: pointer;
  padding: 0px 8px;
  height: 25px;

}

#pesquisa-container span svg {
  height: 25px;
}

#pesquisa-container input {
  border-radius: 10px 10px;
  border: 1px solid #ccc;
  background: #202020;
  border: none;
  width: 80%;
  height: 20px;
  box-sizing: border-box;
  color: #fff;
  padding: 5px 10px;

  outline: none;
}