.Menu-Slider {
    z-index: 47;

    left: 0;
    top: 0;
    width: 100%;
    height: 60px;
    overflow-x: scroll;

    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: left;
    align-items: center;
    gap: 8px;

    padding-inline: var(--inner-width);
}
.Menu-Slider::-webkit-scrollbar {
width: 0;
}

.Menu-Slider-Inner{
    width: 100%;
    height: auto;
}

.Menu-Inner li a {
    text-decoration: none;
    white-space: nowrap;
    z-index: 23;
    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #807E81;
}

.Menu-Inner li svg {
    display: flex;
    align-items: center;
    justify-content: center;
}

.Menu-Inner li {
    outline: none;
    cursor: pointer;
    user-select: none;
    z-index: 23;
}

.Menu-Inner li svg path {
    fill: #737174
}

.Menu-Inner li:active svg path {
    fill: #E8ECEF;
}

.Menu-Inner li:active a {
    color: #E8ECEF;

}


.Menu-Slider li:hover svg path,
.Menu-Slider li:hover a{
    fill: #E8ECEF;
    color: #E8ECEF;
}

.Menu-Slider svg{
    transition: all 0.5s ease-in-out;
}


@media screen and (min-width: 500px) {
    .Menu-Slider{
        display: none;
    }
}
