#root {
    --nav-height: 90px;
    --inner-width: 5%;
}

.Navigation {
    --nav-height: 90px;
}


.NavLinks.active svg path{
    fill: #E8ECEF;
    color: #E8ECEF;
}
.NavLinks.active{
    fill: #E8ECEF;
    color: #E8ECEF;
}

.SeeAll{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: #FFFF;
    font-size: 14px;

    gap: 10px;

    font-family: 'Gilroy-Regular';

    text-decoration: none;
}
.SeeAll svg{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;


}









