.Register-Container {
    margin-top: 18px;

    max-width: 500px;
    height: auto;
    display: flex;
    flex-wrap: wrap;

}

.reg-box {
    width: 361px;
    height: 62px;

    display: flex;
    flex-direction: row;

    background-color: #FFFFFF;

    border-radius: 3px 0px 0px 3px;

    align-items: center;

    justify-content: space-around;

    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

#country {
    width: 30%;
    display: flex;
    margin-left: 24px;

    border: none;
    outline: none;
    appearance: none;

    background: none;
    font-family: 'Gilroy-Regular';
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #0A070B;
}

#name-input {
    border: none;
    outline: none;
    width: 80%;
    height: 70%;
    margin-left: 10px;
    padding-left: 24px;
    border-left: 0.5px solid rgba(244, 27, 60, 0.600);

    font-family: 'Gilroy-Medium';
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #807E81;
}


#name-input.inputVal::-webkit-input-placeholder {
    color: #F41B3B;
}






.reg-btn {
    cursor: pointer;
    width: 138px;
    height: 62px;

    background: #F41B3B;
    border-radius: 0px 3px 3px 0px;

    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.reg-btn:active {
    background-color: #a7142a;
}


.reg-btn p {
    user-select: none;
    text-align: center;
    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
}



@media (max-width: 768px) {

    .Register-Container {
        gap: 16px;
        width: 100%;
        height: 48px;

        font-size: 14px;
        line-height: 22px;
    }

    .reg-box {
        max-width: 361px;
        height: 48px;

        border-radius: 3px 3px 3px 3px;
    }

    .reg-btn {
        width: 138px;
        height: 48px;

        border-radius: 3px 3px 3px 3px;

        font-size: 16px;
        line-height: 22px;
    }
}